const SidebarCategoryList = inject("store")(
  observer(({ store }) => {
    //  constructor(props){
    //    super(props);
    //    state = {
    //     today: "hello"
    //   }
    //  }
    if (store) {
      // let theStore = store;
      // store.changeStartDate("HELLO");
      console.log(store.date + "START");
      // console.log(store.startDate + "THIS STORE");
    }
    function handleClick(value) {
      console.log(value);
      store.changeDate(value);
    }
    return (
      <CategoryList>
        <Item onClick={() => handleClick("today")}>Today</Item>
        <Item onClick={() => handleClick("yesterday")}>Yesterday</Item>
        <Item onClick={() => handleClick("this week")}>This Week</Item>
        <Item onClick={() => handleClick("this month")}>This Month</Item>
        <Item onClick={() => handleClick("custom")}>Custom</Item>
      </CategoryList>
    );
  })
);

export default SidebarCategoryList;

const CategoryList = styled.nav`
  display: flex;
  flex-direction: column;
`;

const Item = styled.div`
  padding: 12px;
  font-size: 15px;
  text-decoration: none;
  color: ${props => props.theme.normalText};
  color: white;
  ::after {
    left: -1px;
    top: 0;
    bottom: 0;
    border-left: 3px solid ${props => props.theme.accent};
  }
  &:hover {
    background-color: #fff;
    color: #212529;
    cursor: pointer;
  }
`;
